<template>
  <el-container>
    <el-main>
      <el-row :gutter="10">
        <el-col :xs="10">
          <video
          id="myVideo"
          class="video-js"
          >
            <source
              src=""
              type="video/mp4">
          </video>
        </el-col>
      </el-row>
    </el-main>
  </el-container>
</template>
<script>
/* eslint-disable */
export default {
  name: 'VideoPlayer',
  data () {
    return {}
  },
  mounted () {
    this.initVideo()
  },
  methods: {
    initVideo() {
      let myPlayer = this.$video(myVideo, {
        //确定播放器是否具有用户可以与之交互的控件。没有控件，启动视频播放的唯一方法是使用autoplay属性或通过Player API。
        controls: true,
        //自动播放属性,muted:静音播放
        autoplay: false,
        //建议浏览器是否应在<video>加载元素后立即开始下载视频数据。
        preload: "auto",
        //设置视频播放器的显示宽度（以像素为单位）
        // width: "100%",
        //设置视频播放器的显示高度（以像素为单位）
        height: "400px",
        fluid: true
      })
    }
  }
}
</script>
